import React from 'react';
import { render, fireEvent } from '@testing-library/react-native';
import { Text, View, StatusBar, TouchableOpacity } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import Header from '../../../src/components/layout/Header';

// Mock StatusBar
jest.mock('react-native', () => {
  const ActualReactNative = jest.requireActual('react-native');
  return {
    ...ActualReactNative,
    StatusBar: {
      ...ActualReactNative.StatusBar,
      setBarStyle: jest.fn(),
      setBackgroundColor: jest.fn(),
    },
  };
});

// Mock useNavigation
jest.mock('@react-navigation/native', () => ({
  useNavigation: jest.fn(),
}));

// Mock Ionicons
jest.mock('@expo/vector-icons', () => ({
  Ionicons: 'Ionicons', // Mock Ionicons as a simple string or a mock component
}));

describe('Header', () => {
  const mockGoBack = jest.fn();
  const mockCanGoBack = jest.fn(() => true);

  beforeEach(() => {
    useNavigation.mockReturnValue({
      goBack: mockGoBack,
      canGoBack: mockCanGoBack,
    });
    jest.clearAllMocks();
  });

  it('renders correctly with a title', () => {
    const { getByText } = render(<Header title="Test Title" />);
    expect(getByText('Test Title')).toBeTruthy();
  });

  it('calls navigation.goBack when back button is pressed and onBackPress is not provided', () => {
    const { getByRole } = render(<Header title="Test Title" showBackButton={true} />);
    fireEvent.press(getByRole('button', { name: 'chevron-back' }));
    expect(mockGoBack).toHaveBeenCalledTimes(1);
  });

  it('calls onBackPress when back button is pressed and onBackPress is provided', () => {
    const customBackPress = jest.fn();
    const { getByRole } = render(<Header title="Test Title" showBackButton={true} onBackPress={customBackPress} />);
    fireEvent.press(getByRole('button', { name: 'chevron-back' }));
    expect(customBackPress).toHaveBeenCalledTimes(1);
    expect(mockGoBack).not.toHaveBeenCalled();
  });

  it('does not render back button when showBackButton is false', () => {
    const { queryByRole } = render(<Header title="Test Title" showBackButton={false} />);
    expect(queryByRole('button', { name: 'chevron-back' })).toBeNull();
  });

  it('renders right icon and calls onRightPress when pressed', () => {
    const customRightPress = jest.fn();
    const { getByText } = render(<Header title="Test Title" rightIcon="settings" onRightPress={customRightPress} />);
    fireEvent.press(getByText('Ionicons')); // Mocked Ionicons renders as 'Ionicons' text
    expect(customRightPress).toHaveBeenCalledTimes(1);
  });

  it('renders custom leftComponent when provided', () => {
    const CustomLeft = () => <Text>Custom Left</Text>;
    const { getByText } = render(<Header title="Test Title" leftComponent={<CustomLeft />} />);
    expect(getByText('Custom Left')).toBeTruthy();
    expect(useNavigation().goBack).not.toHaveBeenCalled(); // Ensure default back behavior is overridden
  });

  it('renders custom rightComponent when provided', () => {
    const CustomRight = () => <Text>Custom Right</Text>;
    const { getByText } = render(<Header title="Test Title" rightComponent={<CustomRight />} />);
    expect(getByText('Custom Right')).toBeTruthy();
  });

  it('renders StatusBar with default props', () => {
    render(<Header title="Test Title" />);
    expect(StatusBar.setBarStyle).toHaveBeenCalledWith('dark-content');
    expect(StatusBar.setBackgroundColor).toHaveBeenCalledWith('#FFFFFF');
  });

  it('renders StatusBar with custom props', () => {
    render(<Header title="Test Title" statusBarColor="#FF0000" statusBarStyle="light-content" />);
    expect(StatusBar.setBarStyle).toHaveBeenCalledWith('light-content');
    expect(StatusBar.setBackgroundColor).toHaveBeenCalledWith('#FF0000');
  });

  it('does not render StatusBar when withStatusBar is false', () => {
    render(<Header title="Test Title" withStatusBar={false} />);
    expect(StatusBar.setBarStyle).not.toHaveBeenCalled();
    expect(StatusBar.setBackgroundColor).not.toHaveBeenCalled();
  });

  it('renders in largeTitleMode with subtitle', () => {
    const { getByText } = render(<Header title="Large Title" largeTitleMode={true} subtitle="Subtitle Text" />);
    expect(getByText('Large Title')).toBeTruthy();
    expect(getByText('Subtitle Text')).toBeTruthy();
  });

  it('renders custom bottomComponent in largeTitleMode', () => {
    const CustomBottom = () => <Text>Custom Bottom</Text>;
    const { getByText } = render(<Header title="Test" largeTitleMode={true} bottomComponent={<CustomBottom />} />);
    expect(getByText('Custom Bottom')).toBeTruthy();
  });

  it('renders children when provided, overriding other content', () => {
    const { getByText, queryByText } = render(<Header title="Should Not See This"><Text>Custom Children</Text></Header>);
    expect(getByText('Custom Children')).toBeTruthy();
    expect(queryByText('Should Not See This')).toBeNull();
  });
});
